})
defineEmits(['sort-by-date'])
function deleteJuvenile(data) {
  Swal.fire({
    title: "Haqiqatdan ham o'chirishni istaysizmi?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#DD6C6C",
    cancelButtonColor: "#06B68C",
    cancelButtonText: "Bekor qilish",
    confirmButtonText: "Ha o'chiraman",
  }).then(async (result) => {
    if (result.isConfirmed) {
      http.delete(`/api/juvenile/juvenile_delete/${data.id}/`).then(() => {
        window.location.reload()
      })
    }
  });
}
